body {
  font-family: Arial;
}

.breed-select {
  text-align: center;
  color: charcoal;
  border-radius: 5px;
  font-family: Arial;
  font-size: 30px;
}

.text {
  max-width: 500px;
}

.cat-info {
  min-height: 70vh;
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
  position: relative;
}

.text-box {
  flex-flow: column;
  justify-content: flex-end;
  display: flex;
}

.cat-info__loader {
  flex-direction: column;
  align-items: center;
  font-weight: 600;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}

.loader {
  width: 70px;
  height: 70px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-color: #000 #000 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 60px;
  height: 60px;
  transform-origin: center;
  border: 3px solid rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #36414f #36414f;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack .7s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:before {
  width: 50px;
  height: 50px;
  border-color: #000 #000 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation: rotation 2s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.hidden {
  display: none;
}

